	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/data-binding-an-array-in-flex/index.md absPath of file >>> MarkdownRemark","html":"<p>Can you do it?  No you can't.</p>\n<p>Not directly anyway - Arrays aren't something that data binding works with in Flex.  ArrayCollection however, is something you <strong>can</strong> work with.  To use an Array as a bindable object, you can wrap it in an ArrayCollection.  It's similar, but not the same as an Array - you can't push, pull, pop or shift things anymore - you have to work with other methods like addItem or removeItem.</p>\n<p>I would've liked to find that all of those classic array methods still worked, but what can you do?</p>","frontmatter":{"title":"Data Binding an Array in Flex?","date":"February 02, 2009","tags":["flex","array","arraycollection"],"url":null}}},"pathContext":{}}

